export const playAudio = async (sprite: string, howl: Howl): Promise<void> => {
  // await audio.play().catch((error) => {
  //   const e = document.createElement('div');

  //   e.innerText = error.message;

  //   document.body.append(e);
  // });
  const id = howl.play(sprite);
  console.log(id, howl.duration(id));
  
  await delay(howl.duration(id) * 1500);
};
